a,
button,
[ng-reflect-router-link],
.btn {
  display: inline-block;
  border: none;
  outline: none;
  cursor: pointer;
  transition: ease-in-out 350ms;
  transition-property: background-color, color, border, opacity;
  /*
	&:hover {
		box-shadow: 0 10px 10px -5px rgba(0, 0, 0, 0.15);
	}
	*/ }
  a, a:active, a:focus, a:hover, a:visited,
  button,
  button:active,
  button:focus,
  button:hover,
  button:visited,
  [ng-reflect-router-link],
  [ng-reflect-router-link]:active,
  [ng-reflect-router-link]:focus,
  [ng-reflect-router-link]:hover,
  [ng-reflect-router-link]:visited,
  .btn,
  .btn:active,
  .btn:focus,
  .btn:hover,
  .btn:visited {
    color: inherit;
    text-decoration: none;
    outline: none; }
  a[disabled],
  button[disabled],
  [ng-reflect-router-link][disabled],
  .btn[disabled] {
    opacity: 0.5;
    pointer-events: none; }

.btn--primary {
  display: inline-block; }
  .btn--primary span {
    display: inline-block;
    padding: 15px 50px;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 600;
    color: #e11e26;
    border: 2px solid #e11e26; }
  .btn--primary span, .btn--primary:active span, .btn--primary:focus span, .btn--primary:hover span, .btn--primary:visited span {
    color: #e11e26;
    border-color: #e11e26; }
  .section--negative .btn--primary span {
    color: #ffffff;
    border-color: #ffffff; }
  .section--negative .btn--primary span, .section--negative .btn--primary:active span, .section--negative .btn--primary:focus span, .section--negative .btn--primary:hover span, .section--negative .btn--primary:visited span {
    color: #ffffff;
    border-color: #ffffff; }
