[canvas] {
  position: relative;
  opacity: 0;
  transition: opacity ease-in-out 600ms; }
  [canvas] .inner {
    position: absolute;
    width: 100vw;
    height: 100vh;
    max-width: 1080px;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    transition: left ease-in-out 1200ms;
    pointer-events: none;
    z-index: 100; }
    [canvas] .inner.lefted {
      left: 12.5%; }
    [canvas] .inner canvas {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; }
  [canvas].fixed .inner {
    position: fixed; }
  [canvas].loaded {
    opacity: 1; }
  [canvas] .debug__info {
    position: absolute;
    left: 20px;
    bottom: 20px;
    padding: 8px 8px;
    border-radius: 3px;
    font-size: 13px;
    font-weight: 600;
    background: rgba(255, 255, 255, 0.9);
    color: black;
    display: none; }

